}
footer{
     background-color: #fbd3d3;
    position:absolute;
    bottom:0px; 
    width: 100vw;
}
footer .contacto{  
    box-shadow: -1px -1px 3px rgb(54, 53, 53);
    display: flex;
    justify-content: space-between;
    align-items: center;
    /* border: 1px solid red; */
}

footer .contacto .iconos{ 
  align-items: center;
  font-size: 0px;
  padding-right: 30px;
}
footer .contacto p{ 
    color:rgb(70, 68, 68);
    font-size: 18px;
    padding-left: 30px;
    padding-top:10px;
}
footer .contacto i{ 
    color:rgb(70, 68, 68);
    font-size: 26px;
