.layout {
  
  height: 100vh;
  max-width: 500px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-family: "Hina Mincho", serif;
}

.historia {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 30px;
  color: white;
}

.opciones {
  color: white;
}

.opcion {
  display: flex;
  grid-gap: 15px;
  gap: 15px;
  margin-bottom: 10px;
}

.recordatorio {
  color: white;
  margin-top: 60px;
}

.botones {
  box-shadow: inset 0px 1px 0px 0px #ffffff;
  background: linear-gradient(to bottom, #f9f9f9 5%, #e9e9e9 100%);
  background-color: #f9f9f9;
  border-radius: 6px;
  border: 1px solid #dcdcdc;
  display: inline-block;
  cursor: pointer;
  color: #000000;
  font-family: Arial;
  font-size: 15px;
  font-weight: bold;
  padding: 6px 24px;
  text-decoration: none;
  text-shadow: 0px 1px 0px #ffffff;
}
.botones:hover {
  background: linear-gradient(to bottom, #e9e9e9 5%, #f9f9f9 100%);
  background-color: #e9e9e9;
}
.botones:active {
  position: relative;
  top: 1px;
}

.app-perritos{

    height: 100vh;
    display: flex;
    flex-direction: column;
    font-family: 'Poppins';
    background-color: #FAEEC7
}

.header-perritos {
    padding: 10px 15px;
    text-align: center;
}

.header-perritos h1 {
    font-size: 24px
}

.main-perritos {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    flex-grow: 1;
